import clsx from "clsx";
import React from "react";
import { TagProps, TagVariant } from "./Tag.types";
import "./Tag.scss";

const Tag: React.FC<TagProps> = ({ variant, iconLeft, label, className }) => {
  const styling = clsx({
    c4tag: true,
    "tag--white": variant === TagVariant.WHITE,
    "tag--default": variant === TagVariant.DEFAULT,
    "tag--blurple": variant === TagVariant.BLURPLE,
    "tag--red": variant === TagVariant.RED,
    "tag--yellow": variant === TagVariant.YELLOW,
    "tag--white-outline": variant === TagVariant.WHITE_OUTLINE,
  });

  return (
    <div className={`${styling} ${className}`}>
      {iconLeft && <img src={iconLeft} width={16} height={16} />}
      {label}
    </div>
  );
};

export default Tag;
